import React, { Component } from 'react'
import './index.css'

export default class Search extends Component {

  constructor(){
    super()
    this.state = {
      focus: false
    }
  }

  handSeacrch(e){
    if(e.keyCode === 13&&e.target.value!==''){
      window.open('//www.baidu.com/s?wd=' + e.target.value)
    }
  }

  inputOnBlur = ()=>{
    this.setState({
      focus:false
    })
  }

  inputOnFocus = ()=> {
    this.setState({
      focus:true
    })
  }

  render() {
    return (
      <div className='searchBox'>
        <div className={`birthday ${this.state.focus?'move':''}`}>
          <img src={require('../../static/searchTop.png')} alt='' />
        </div>
        <input className='searchInput'onKeyDown={(e)=>{this.handSeacrch(e)}} type='text' placeholder='emmm' onFocus={this.inputOnFocus} onBlur={this.inputOnBlur}/>
        <div className="go"></div>
      </div>
    )
  }
}
