import React, { Component } from 'react'
import './index.css'

export default class Weather extends Component {
  constructor() {
    super()
    this.state = {
      weather: {
        now: {
          tmp:'',
          cond_txt:'',
          location:''
        },
        basic: {}
      },
      tips: '',
      iconImg: '',
      time:'',
      date:'',
      day:''
    }
  }
  componentWillMount() {

    fetch('https://free-api.heweather.net/s6/weather/now?location=shiyan&key=540136696a6840aa85cc8b67065a3ffb').then((res) => {
      return res.json()
    }).then((data) => {
      if (data.HeWeather6 && data.HeWeather6[0].status === 'ok') {
        let iconImg = `https://cdn.heweather.com/cond_icon/${data.HeWeather6[0].now.cond_code}.png`
        this.setState({
          weather: data.HeWeather6[0],
          tips: '我不在身边要好好照顾自己嘤嘤嘤QAQ',
          iconImg
        })
      }
    })

    this.timer = setInterval(() => {
      let nowDay = new Date().getDay();
      let dayArry = ['一','二','三','四','五','六','日'];
      let day = dayArry[nowDay-1]
      this.setState({
        time: new Date().toLocaleTimeString(),
        date: new Date().toLocaleDateString(),
        day
      })
    }, 100)

  }
  componentWillUnmount() {
    clearInterval(this.timer)
  }
  render() {
    return (
      <div className='wrap'>
        <div className='timeBoxs'>
          <div className='time'>{this.state.time}</div>
          <div className='date'>{`${this.state.date}  星期${this.state.day}`}</div>
        </div>
        <div className='weather'>
          <div className='top'>
            <span className='temp'>{`${this.state.weather.now.tmp}℃`}</span>
            <span className='cond'>{this.state.weather.now.cond_txt}</span>
            <img alt='' src={this.state.iconImg} className='weatherIcon' />
          </div>
          <div className='bottom'>
            {this.state.weather.basic.location}
          </div>
        </div>
        {/* <div className='tips'>
          {this.state.tips}
        </div> */}
      </div>
    )
  }
}
