import React, { Component } from 'react'
import './index.css'

class Gif extends Component{
    constructor(){
        super()
        this.state = {
            gifUrl:[
                require('../../static/gif1.gif'),
                require('../../static/gif2.gif'),
                require('../../static/gif3.gif'),
                require('../../static/gif4.gif'),
                require('../../static/gif5.gif'),
                require('../../static/gif6.gif'),
                require('../../static/gif7.gif'),
                require('../../static/gif8.gif'),
                require('../../static/gif9.gif'),
                require('../../static/gif10.gif'),
            ]
        }
    }

    render(){
        return(
            <ul className='gifBox'>
                {
                    this.state.gifUrl.map((item,index) => {
                        return (
                            <li key={index} className='gifItem'>
                                <img src={item} alt=''/>
                            </li>
                        )
                    })
                }
            </ul>
        )
    }
}

export default Gif